import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useHistory } from 'react-router';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { useTranslation } from 'react-i18next';
import memberService from '../../../services/memberService';
import useSnackbar from '../../../hooks/useSnackbar';

const useStyles = makeStyles(() => ({
  root: {}
}));

function MemberEditForm({ className, member, ranks, ...rest }) {
  const classes = useStyles();
  const { successSnackbar } = useSnackbar();
  const { t } = useTranslation();
  const history = useHistory();

  return (
    <Formik
      initialValues={{
        email: member.email || '',
        first_name: member.first_name || '',
        last_name: member.last_name || '',
        phone: member.phone || '',
        password: member.password || '',
        address: member.address || '',
        remark: member.remark || '',
        gender: member.gender || '',
        rank_id: member.rank_id || ''
      }}
      validationSchema={Yup.object().shape({
        email: Yup.string()
          .email(t('Must be a valid email'))
          .max(255),
        first_name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Name') })),
        last_name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Name') })),
        phone: Yup.string().max(15),
        password: Yup.string()
          .min(6, t('Must be at least 6 characters'))
          .max(255)
      })}
      onSubmit={async (values, { resetForm, setErrors, setStatus, setSubmitting }) => {
        try {
          await await memberService.update(member.id, {
            ...values,
            id: member.id
          });
          setStatus({ success: true });
          setSubmitting(false);
          successSnackbar(t('Updated', { name: t('Member') }));
          history.push('/members');
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit} {...rest}>
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.first_name && errors.first_name)}
                    fullWidth
                    helperText={touched.first_name && errors.first_name}
                    label={t('First Name')}
                    name="first_name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.first_name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.last_name && errors.last_name)}
                    fullWidth
                    helperText={touched.last_name && errors.last_name}
                    label={t('Last Name')}
                    name="last_name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.last_name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.phone && errors.phone)}
                    fullWidth
                    helperText={touched.phone && errors.phone}
                    label={t('Phone')}
                    name="phone"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.phone}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.email && errors.email)}
                    fullWidth
                    helperText={touched.email && errors.email}
                    label={t('Email address')}
                    name="email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.email}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.password && errors.password)}
                    fullWidth
                    helperText={touched.password && errors.password}
                    label={t('Password')}
                    name="password"
                    type="password"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.password}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Ranks')}
                    name="rank_id"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.rank_id}
                    variant="outlined"
                  >
                    {ranks.map((rank) => (
                      <option key={rank.id} value={rank.id}>
                        {rank.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.address && errors.address)}
                    fullWidth
                    helperText={touched.address && errors.address}
                    label={t('Address')}
                    name="address"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.address}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.remark && errors.remark)}
                    fullWidth
                    helperText={touched.remark && errors.remark}
                    label={t('Remark')}
                    name="remark"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.remark}
                    variant="outlined"
                  />
                </Grid>
                <Grid item />
                <Grid item md={6} xs={12}>
                  <FormControl component="fieldset">
                    <FormLabel component="legend">{t('Gender')}</FormLabel>
                    <RadioGroup
                      name="gender"
                      value={values.gender}
                      onBlur={handleBlur}
                      onChange={handleChange}
                    >
                      <FormControlLabel value="female" control={<Radio />} label={t('Female')} />
                      <FormControlLabel value="male" control={<Radio />} label={t('Male')} />
                      <FormControlLabel value="other" control={<Radio />} label={t('Other')} />
                      <FormControlLabel value="unknown" control={<Radio />} label={t('Unknown')} />
                    </RadioGroup>
                  </FormControl>
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

MemberEditForm.propTypes = {
  className: PropTypes.string,
  member: PropTypes.object.isRequired,
  ranks: PropTypes.array.isRequired
};

export default MemberEditForm;
